	module.exports = {"data":{"markdownRemark":{"html":"<h2>Heroku</h2>\n<p>Heroku is a cloud platform as a service (PaaS) supporting several programming languages that is used as a web application deployment model. Heroku, one of the first cloud platforms, has been in development since June 2007, when it supported only the Ruby programming language, but now supports Java, Node.js, Scala, Clojure, Python, PHP, and Go.<a href=\"\">1</a> For this reason, Heroku is said to be a polyglot platform as it lets the developer build, run and scale applications in a similar manner across all the languages. Heroku was acquired by Salesforce.com in 2010 for $212 million </p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href='https://en.wikipedia.org/wiki/Heroku' target='_blank' rel='nofollow'>Wikipedia</a></li>\n</ul>","fields":{"slug":"/cloud-development/Heroku/"},"frontmatter":{"title":"Heroku"}}},"pathContext":{"slug":"/cloud-development/Heroku/"}}

